class DynamicAdapt {
  constructor(type) {
    this.type = type;
  }

  init() {
    //  
    this.bjects = [];
    this.daClassname = "_dynamic_adapt_";
    //  DOM-
    this.nodes = [...document.querySelectorAll("[data-da]")];

    //  bjects 
    this.nodes.forEach((node) => {
      const data = node.dataset.da.trim();
      const dataArray = data.split(",");
      const bject = {};
      bject.element = node;
      bject.parent = node.parentNode;
      bject.destination = document.querySelector(`${dataArray[0].trim()}`);
      bject.breakpoint = dataArray[1] ? dataArray[1].trim() : "767";
      bject.place = dataArray[2] ? dataArray[2].trim() : "last";
      bject.index = this.indexInParent(bject.parent, bject.element);
      this.bjects.push(bject);
    });

    this.arraySort(this.bjects);

    //   -
    this.mediaQueries = this.bjects
      .map(
        ({ breakpoint }) =>
          `(${this.type}-width: ${breakpoint}px),${breakpoint}`
      )
      .filter((item, index, self) => self.indexOf(item) === index);

    //    -
    //      
    this.mediaQueries.forEach((media) => {
      const mediaSplit = media.split(",");
      const matchMedia = window.matchMedia(mediaSplit[0]);
      const mediaBreakpoint = mediaSplit[1];

      //     
      const bjectsFilter = this.bjects.filter(
        ({ breakpoint }) => breakpoint === mediaBreakpoint
      );
      matchMedia.addEventListener("change", () => {
        this.mediaHandler(matchMedia, bjectsFilter);
      });
      this.mediaHandler(matchMedia, bjectsFilter);
    });
  }

  //  
  mediaHandler(matchMedia, bjects) {
    if (matchMedia.matches) {
      bjects.forEach((bject) => {
        bject.index = this.indexInParent(bject.parent, bject.element);
        this.moveTo(bject.place, bject.element, bject.destination);
      });
    } else {
      bjects.forEach(({ parent, element, index }) => {
        if (element.classList.contains(this.daClassname)) {
          this.moveBack(parent, element, index);
        }
      });
    }
  }

  //  
  moveTo(place, element, destination) {
    element.classList.add(this.daClassname);
    if (place === "last" || place >= destination.children.length) {
      destination.append(element);
      return;
    }
    if (place === "first") {
      destination.prepend(element);
      return;
    }
    destination.children[place].before(element);
  }

  //  
  moveBack(parent, element, index) {
    element.classList.remove(this.daClassname);
    if (parent.children[index] !== undefined) {
      parent.children[index].before(element);
    } else {
      parent.append(element);
    }
  }

  //     
  indexInParent(parent, element) {
    return [...parent.children].indexOf(element);
  }

  //     breakpoint  place
  //    this.type = min
  //    this.type = max
  arraySort(arr) {
    if (this.type === "min") {
      arr.sort((a, b) => {
        if (a.breakpoint === b.breakpoint) {
          if (a.place === b.place) {
            return 0;
          }
          if (a.place === "first" || b.place === "last") {
            return -1;
          }
          if (a.place === "last" || b.place === "first") {
            return 1;
          }
          return a.place - b.place;
        }
        return a.breakpoint - b.breakpoint;
      });
    } else {
      arr.sort((a, b) => {
        if (a.breakpoint === b.breakpoint) {
          if (a.place === b.place) {
            return 0;
          }
          if (a.place === "first" || b.place === "last") {
            return 1;
          }
          if (a.place === "last" || b.place === "first") {
            return -1;
          }
          return b.place - a.place;
        }
        return b.breakpoint - a.breakpoint;
      });
      return;
    }
  }
}

const da = new DynamicAdapt("max");
da.init();

export default DynamicAdapt;
